import React, { useState } from "react";
// import logo from "../img/logo.jpg";
import "../../styles/Navbar.css";
import { Link, Navigate } from "react-router-dom";
import MenuIcon from '@mui/icons-material/Menu';
import CloseIcon from '@mui/icons-material/Close';
import KeyboardArrowDownIcon from '@mui/icons-material/KeyboardArrowDown';
import spainFlag from '../../img/spain.png'
import ukFlag from '../../img/uk.png'

export default function Navbar() {

  const [togglerActive, setTogglerActive] = useState(false);
  // const [language, setLanguage] = useState(localStorage.getItem("language") || "eng");

  const handleLanguage = (language) => {
    localStorage.setItem("language", language);
    // language === "eng" ? <Navigate to='/'/> : <Navigate to='/es'/>
  }

  const handleToggler = ()=>{
    setTogglerActive(!togglerActive);
  }

  // useEffect(()=>{
  //   console.log("npm starchanged")
  // }, [togglerActive])

  return (
    <div>
      <nav className="nav">
        <Link to="/" className="logo-link">
          {/* <img src={logo} alt="logo" className="brand" /> */}
        </Link>
        <ul className="nav-ul">
          <li>
          <a href="#main"> Inicio </a>
          </li>
          <li>
            <a href="#portfolio"> Portafolio </a>
          </li>
          <li>
            <a href="#about"> Acerca de mi </a>
          </li>
          <li>
            <a href="#contact"> Contacto </a>
          </li>
          <li className="nav-dropdown">
            <a href="#"> Lenguaje </a>
            <KeyboardArrowDownIcon/>
            <ul className="nav-dropdown-menu">
              <Link to='/'><li className="language-group" onClick={()=> handleLanguage("eng")}>
                <span> Inglés </span>
                <img src={ukFlag} alt="spanish flag" />
              </li></Link>
              <Link to='/es'><li className="language-group" onClick={()=> handleLanguage("spa")}>
                <span> Español </span> 
                <img src={spainFlag} alt="united kingdom flag" />
              </li></Link>
            </ul>
          </li>
        </ul>
        <span className="toggler-btn" onClick={()=> handleToggler() }>
          {togglerActive === false ? <MenuIcon /> : <CloseIcon/>}
        </span>
        <ul className={ togglerActive === false ? "menu-h-hidden" : "menu-h-visible menu-h" }>
          <li>
          <a href="#main" onClick={handleToggler}> Inicio </a>
          </li>
          <li>
            <a href="#portfolio" onClick={handleToggler}> Portafolio </a>
          </li>
          <li>
            <a href="#about" onClick={handleToggler}> Acerca de mi </a>
          </li>
          <li>
            <a href="#contact" onClick={handleToggler}> Contacto </a>
          </li>
        </ul>
      </nav>
    </div>
  );
}
