import React, { useRef, useState } from 'react'
import emailjs from '@emailjs/browser';
import '../styles/ContactForm.css'
import SendIcon from '@mui/icons-material/Send';
import WarningAmberRoundedIcon from '@mui/icons-material/WarningAmberRounded';
import CheckCircleOutlineIcon from '@mui/icons-material/CheckCircleOutline';

export default function ContactForm() {
    
    const [name, setName] = useState(""); 
    const [email, setEmail] = useState(""); 
    const [message, setMessage] = useState(""); 
    const [nameErrorMessage, setNameErrorMessage] = useState("")
    const [emailErrorMessage, setEmailErrorMessage] = useState("")
    const [messageErrorMessage, setMessageErrorMessage] = useState("")
    const [sendWarning, setSendWarning] = useState(false);
    const [messageSended, setMessageSended] = useState(false);

    const form = useRef();
    const regExp = {
        name: /^([a-zA-Z]{2,})\s?([a-zA-Z]{2,})\s?([a-zA-Z]{2,})?\s?([a-zA-Z]{2,})?\s?([a-zA-Z]{2,})?\s?([a-zA-Z]{2,})?$/,
        email: /^([A-Za-z0-9_\-\.])+\@([A-Za-z0-9_\-\.])+\.([A-Za-z]{2,4})$/
    }

    if (sendWarning === true ) {
        setTimeout(()=>{ setSendWarning(false) },3000)
    }
    if (messageSended === true ) {
        setTimeout(()=>{ setMessageSended(false) },5000)
    }

    const validateName = () => {
        if  (   regExp.name.test(name) ) {
            return true;
        }
        else { 
            return false;
        }
    }
    const validateEmail = () => {
        if  (   regExp.email.test(email) ) {
            return true;
        }
        else { 
            return false;
        }
    }
    const validateMessage = () => {
        if  (  message !== "" ) {
            return true;
        }
        else { 
            return false;
        }
    }

    const validateFields = () => {
        (validateName() === false) ? setNameErrorMessage('Enter a valid name.') : setNameErrorMessage(""); 
        (validateEmail() === false) ? setEmailErrorMessage('Enter a valid email.') : setEmailErrorMessage("");
        (validateMessage() === false) ? setMessageErrorMessage('Please, write a message.') : setMessageErrorMessage("");  
        if (validateName() ===  true && validateEmail() === true && validateMessage()) {
            return true;
        } 
    }

    const resetFields = () => {
        setName("");
        setEmail("");
        setMessage("");
    }

    const sendEmail = () => {
        emailjs.sendForm('service_3vrlpd6', 'template_0ywdl6g', form.current, 'mV8i5LkxjboUdtGxo')
        .then((result) => {
            console.log(result.text);
            setMessageSended(true);
            resetFields();
        }, (error) => {
            console.log(error.text);
        })
    } 
   
    const handleSubmit = (e) => {
        e.preventDefault();
        const validated = validateFields();
        if (validated === true) { 
            sendEmail()
        }
        else { setSendWarning(true)
        };
    }
  
  return (
    <form className='contact-form' ref={form} onSubmit={handleSubmit}>
        <div className='contact-form__group'>
            <label > Your name </label>
            <input type="text" maxLength="50" name="user_name" placeholder='Full Name' value={name} onChange={(e)=> setName(e.target.value)}/> 
            { nameErrorMessage && <p className='invalidInputMessage'> {nameErrorMessage} </p> }
        </div>
        <div className='contact-form__group'>
            <label > Your email </label>
            <input type="email"  maxLength="50" name="user_email" placeholder='email@example.com' value={email} onChange={(e)=> setEmail(e.target.value)}/>
            { emailErrorMessage && <p className='invalidInputMessage'> {emailErrorMessage} </p> }
        </div>
        <div className='contact-form__group--text-area'>
            <label > Your message </label>
            <textarea name="message" placeholder="Write your consult here..." maxLength="1000" id="" cols="30" rows="10" value={message} onChange={(e)=> setMessage(e.target.value)}></textarea>
            { messageErrorMessage && <p className='invalidInputMessage'> {messageErrorMessage} </p> }
        </div>
        { sendWarning === true && 
        <div className='submit-warning'>
            <p> Correct the invalid fields to send the message </p> 
            <WarningAmberRoundedIcon/>
        </div>
        } 
        <div className='contact-form__group--button'>
        {messageSended 
        ? <button className='green-button'> Message Sended <CheckCircleOutlineIcon/></button>
        : <button type="submit" value="Send" className='dark-button'> Send <SendIcon/></button>
        }
        </div>
    </form>
  )
}
